// colors

$grey--light : #f1f1f1;
$grey--dark : darken($grey--light , 40%);
$white : #fff;
$black : #000;
$primary : #d00155; 
$secondary:#50dae2;
$media-breakpoint : 45em;
$baseFont :14px;
// globals

$w-100: 100%; 

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
    
}



// common classes

// colors

.clr-p { color: $primary;}
.clr-s { color: $secondary;}
.clr-f { color: $white;}

.bg-b { background-color: $black;}
.bg-p { background-color: $primary;}
.bg-s { background-color: $secondary;}
.bg-f { background-color: $white;}

// Positon
.p-R { position:relative;}
.p-F { position:fixed;}
.p-A {position:absolute;}
.p-I {position:inherit;}

// align set

._oT {top:0;}
._oR {right:0;}
._oL {left:0;}
._oB {bottom:0;}

// text orientations 
._L { text-align: left; }
._R { text-align: right; }
._C { text-align: center; }
._J { text-align: justify; }

// z indexing definitions

._z9 {z-index: 9;}
._z99 {z-index: 99;}
._z999 {z-index: 999;}

.dB {display:block;}
.dIB{ display:inline-block;}
.dI{ display:inline;}
.dF{ display:flex;}
.dIF{ display:inline-flex;}
.hide {display:none;}
@media screen and (max-width:$media-breakpoint){
  .hide-s { display: none;}
}


.bdr-1 {border:1px solid #999;}
.bdr-0 {border:0;}

.hand {cursor:pointer;}
.camelCase {text-transform: capitalize;}
.uCase {text-transform: uppercase;}
.w-100 { width:$w-100;}


.easy {  -webkit-transition: all 0.2s ease-in-out; transition: all 0.2s ease-in-out;}

// fonts


@function rem($pixels, $context: $baseFont) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }

  @if (unitless($context)) {
    $context: $context * 1px;
  }

  @return $pixels / $context * 1rem;
}


$i:40 ;
@while $i >= 10 {
    .fs-#{$i} { font-size:rem($i); } // font size class from fs-10 to fs-40
    $i: $i - 2 ;
}



.fw-7 { font-weight:700;}
.fw-6 { font-weight:600;}
.fw-5 { font-weight:500;}
.fw-4 { font-weight:400;}

// padding
.p-1 {padding:1rem;} 
.p-half {padding:0.5rem;} 

// margin

.m-y-1 {margin: 1rem 0;}
